body {
  margin: 0;
  padding: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url(/images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :first-child,
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :not(:last-child),
.stars--5 > * {
  background: url(/images/star-active.svg) center no-repeat;
}
